import React, { useState } from 'react';
import './App.css';
import { TextField, Button, Stack } from '@mui/material';
import { Header } from './components/Header';
import DeleteOutlineIcon from '@mui/icons-material/DeleteOutline';

function App() {
  const [name, setName] = useState("")
  const [email, setEmail] = useState("")
  const [data, setData] = useState([])

  const addData = () => {
    if(name&&email){
    setData([...data, {
      name: name,
      email: email
    }])
    setName("")
    setEmail("")
  }

  else{
    alert("Enter a valid name and email ID")
  }
  }
  const removeItem = (index) => {
    let arr = data
    arr.splice(index, 1)
    setData([...arr])
  }
  return (
    <div className="App">
      <Header />
      <div className='form'>
        <Stack direction={'row'} spacing={2}>

          <TextField
            value={name}
            onChange={(e) => { setName(e.target.value) }}
            id="outlined-basic"
            label="Name"
            variant="outlined"
          />

          <TextField
            value={email}
            onChange={(e) => { setEmail(e.target.value) }}
            id="outlined-basic"
            label="e-mail"
            variant="outlined"
          />

          <Button onClick={addData} variant="contained" color="success">Add</Button>
        </Stack>
      </div>

      <div className='data-display'>
        <div className='data'>
          <h4>Name</h4>
          <h4>e-mail</h4>
          <h4>remove</h4>
        </div>
        {
          data.map((e, index) => {
            return (
              <div className='data' key={index} >
                <h4>{e.name}</h4>
                <h4>{e.email}</h4>
                <Button variant="outlined" color="error" onClick={() => removeItem(index)} >
                  <DeleteOutlineIcon />
                </Button>
              </div>
            )
          })
        }
      </div>
    </div>
  );
}

export default App;
